@import '../../scss/mixins';

.Webshop {
	display: flex;
}
.Buttons {
	display: flex;
	justify-content: space-around;
	color: #007aff;
	font-size: 50px;
	margin-left: 150px;
	margin-bottom: 70px;
	.Previous,
	.Next {
		transition-duration: .15s;
		&:hover {
			color: darken(#007aff, 10%);
			cursor: pointer;
		}
		&:active {
			color: darken(#007aff, 20%);
			cursor: grabbing;
		}
	}
}

@include phone {
	.Webshop {
		display: block;
	}
	.Buttons {
		margin: 0;
		margin-bottom: 70px;
	}
}
