@import '../../../scss/mixins';

.PassReset {
	background-color: white;
	margin: 100px auto 0 auto;
	border: 1px solid #dedede;
	width: 500px;
	height: 500px;
	color: #4267b2;
	-webkit-box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	h3 {
		text-align: center;
		font-size: 1.5em;
		margin: 50px 0 75px 0;
	}
	.Label {
		margin-left: 100px;
	}
	.Button {

        text-align: center;
		button {
			margin-top: 50px;
            width: 200px;
			&:hover {
				background-color: #4267b2;
			}
			&:active {
				background-color: darken(#4267b2, 10%);
				cursor: grabbing;
			}
		}
	}
}



@include phone {
    .PassReset{
		margin: 0;
		width: 100%;
		height: 100%;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		border: none;
		.Label{
			margin-left: 5%;
		}
		.Footer{
			a{
				margin: 90px 10px 0 10px;
			}
		}
	}
}
 