@import '../../../scss/mixins';

.AddComment {
	border-bottom: 1px solid #dedede;
	.Title {
		text-align: left;
	}
	.Comment {
		textarea {
			outline: none;
			border: 1px solid #ccc;
			background-color: white;
			padding: 6px 10px;
			display: block;
			width: 60%;
			margin: 0 auto 30px auto;
			border-radius: 5px;
			min-width: 150px;
		}
	}
	.Button {
		width: 150px;
		margin-left: 0px;
	}
}

@include phone {
	.AddComment {
		.Comment {
			textarea {
				margin: 0;
				min-width: 90%;
			}
		}
	}
}
